import { DefinitionIndeCanvasCore } from '../Core/DefinitionIndeCanvasCore';

import { TFProject } from '..';

// import * as tf from '@tensorflow/tfjs';
import { NVector3 } from '../../../lib';
import { TFUtility } from '../../../../../../../lib/NJSCore/ML/NMacineLearning';
export class TFPlayground01 extends DefinitionIndeCanvasCore {

    xpos: number = 100;
    ypos: number = 100;

    xspeed: number = 10;
    yspeed: number = 10;

    xdirection: number = 1;
    ydirection: number = 1;

    rad: number = 50;
    vs: NVector3[] = [];

    a: number = 1;
    bias: number = 0;

    constructor(tfProjct: TFProject) {
        super(tfProjct);
        this.indeCanvas.title = 'TF Playground';
        this.Init();
    }
    Init() {
        // console.log(tf.version);
        // this.Tensor01();
        // this.Tensor02();
        // this.Tensor03();
        TFUtility.Playground();
    }

    MouseDown(x: number, y: number) {
        //
    }
    RenderIndependentCanvas(ctx: CanvasRenderingContext2D) {
        ctx.fillStyle = '#000000';
        ctx.beginPath();
        ctx.rect(0, 0, ctx.canvas.width, ctx.canvas.height);
        ctx.closePath();
        ctx.fill();

    }
}